<template lang="pug">
  #eassy
    navbar
    main
      button.btn_backToTop.mb_hide(@click="backToTop")
      ad
      newRecommend
      #ad_none
        .ad_inner
          a.mb_hide(href='#')
            img(src='../assets/images/ad_h_s.jpg')
          a.pc_hide(href='#')
            img(src='../assets/images/ad_s.jpg')

</template>

<script>
import $ from 'jquery';
import navbar from '../components/navbar.vue';
import ad from '../components/ad.vue';
import newRecommend from '../components/eassy/newRecommend.vue';

export default {
  components: {
    navbar,
    ad,
    newRecommend,
  },
  methods: {
    backToTop() {
      $('html, body').animate({ scrollTop: 0 }, 200);
    },
  },
  mounted() {
    const $win = $(window);
    const $backToTop = $('.btn_backToTop');
    $win.scroll(() => {
      if ($win.scrollTop() > 200) {
        $backToTop.show();
      } else {
        $backToTop.hide();
      }
    });
  },
};
</script>

